	import Vue from 'vue'
	import listItem from '@/components/common/ListItem';
	import radio from '@/components/common/rxRadio';
	import checkbox from '@/components/common/rxCheckbox';
	import {loadRefreshList} from '@/assets/app.js';
	import router from '@/router/index';
	import {RxUtil} from "@/assets/util.js";
	
	Vue.component(listItem.name, listItem);
	Vue.component(radio.name, radio);
	Vue.component(checkbox.name, checkbox);
	
	export default {
		name : "rx-sysuser",
		data(){
		    return {
		      	list:[],
				page:0,
				total:0,
				searchName:"",
				merage:true,
				historyParams:[],
				
				selectUserId:"",
				selectedUser:[]
		    }
		},
		computed:{
			showBar:function(){
				return this.$store.state.pageModule.keyboard;
			}
		},
		props:{
		  	single:{
		  		type: Boolean, 
		  		default: true
		  	}
		},
		created() {
  			this.searchName="";
	  		this.historyParams=[];
		},
		computed: {
	  		classBtn: function () {
	  			var rtn={
	      			single: this.single,
	      			multity: !this.single
	    		};
	    		return rtn;
	  		},
	  		selectAmount: function(){
		    	if(!this.selectUserId) return 0;
		    	return this.selectUserId.split(",").length;
		    }
		},
		methods:{
		    loadList(){
		    	var url=_baseUrl +"/mobile/oa/getDimen.do";
				var params="pageIndex="+this.page+"&pageSize=10";
				this.loadLevel(params,url);
	
		    },
		    search(){
		    	this.page=0;
		    	this.merage=false;
		    	this.loadList();
		    },
		    cancel(){
		    	this.$emit('cancel');
		    },
		    //设置选中的帐号。
		    setValue(val){
		    	this.selectUserId=val;	    	
		    },
		    handleClick(item,index){
		    	console.log(index)
		    	console.log(item)
		    	if(item.dimId){
		    		var url=_baseUrl +"/mobile/oa/getGroup.do";
		    		var params={
		    			dimId:item.dimId
		    		};
					this.loadLevel(params,url);
		    	} else if(item.groupId&&item.childs>0){
		    		var url=_baseUrl +"/mobile/oa/getGroup.do";
	 				var params={
		    			groupId:item.groupId
		    		};
					this.loadLevel(params,url);
		    	} else if(item.groupId&&(item.childs==0||item.childs==null)){
		    		var url=_baseUrl +"/mobile/oa/getUsers.do";
	 				var params={
		    			groupId:item.groupId
		    		};
					this.loadLevel(params,url);
		    	}
		    	//this.$emit('ok',this.selectedUser);
		    	//console.log(1111111)
		    },
		    loadLevel(params,url){
				this.$ajax({
					url: url,
					method: 'POST',
					params:params
				}).then(response => {
					this.list = response.data.rowList;
					var flag=false;
					for(var i=0;i<this.historyParams.length;i++){
						var arr=this.historyParams[i];
						if(arr.params==params&&arr.url==url){
							flag=true;
						}
					}
					if(!flag){//不包含
						var obj={};
						obj.params=params;
						obj.url=url;
						this.historyParams.push(obj);
					}
				}).catch(error => {
					console.log(error);
				});
		    },
		    historyList(){
		    	if(this.historyParams.length>=2){
		    		var obj=this.historyParams[this.historyParams.length-2];
		    		this.loadLevel(obj.params,obj.url);
		    		this.historyParams.splice(this.historyParams.length-1,1);
		    	} else if (this.historyParams.length>=1) {
		    		this.cancel();
		    	}
		    },
		    searchByName(){
		    	if(this.searchName){
		    		var url=_baseUrl +"/mobile/oa/getUsersSearch.do";
					var params="";
					if(this.searchName) {
						params="&Q_fullName_S_LK=" + this.searchName;
					}
					this.$ajax.post(url, params).then(response => {
						this.list=response.data.rowList;
					})
					.catch(function(error) {
					});
		    	}
		    },
			getFilePath(fileId) {
				return _baseUrl + "/sys/core/file/previewFile.do?fileId=" + fileId;
			},
		    ok(){
		    	this.$emit('ok',this.selectedUser);
		    	this.$store.commit('srtFromUser', this.selectedUser);
		    	console.log(this.selectedUser)
		    }
	    
		},
		watch: {
	    	selectUserId: function (val, oldVal) {
	    		if(!val){
	    			this.selectedUser=[];
	    			return;
	    		}
	    		//处理增加的用户，意思是当前选择的用户不在选择的列表中。
	    		var aryVal=this.selectUserId.split(",");
	    		var selectMap=RxUtil.covertList2Map("userId", this.selectedUser);
	    		var allMap=RxUtil.covertList2Map("userId", this.list);
	    		//当前用户不在选中的用户中，添加
	    		for(var i=0;i<aryVal.length;i++){
	    			var tmp=aryVal[i];
	    			if(!selectMap[tmp]){
	    				this.selectedUser.push(allMap[tmp]);
	    			}
	    		}
	    		//删除已选
	    		for(var i=this.selectedUser.length-1;i>=0;i--){
	    			var o=this.selectedUser[i];
	    			var id=o["userId"];
	    			var isIn=RxUtil.isInAry(aryVal,id);
	    			if(!isIn){
	    				this.selectedUser.splice(i,1);
	    			}
	    		}
	    		
	    	}
    	}
	}



