export default{
    name : "rx-radio",
    props: ["value","val","name"],
 	mounted() {
	    this.$refs.input.checked=this.isChecked()
	},
	methods: {
	    change(value) {
	      this.$refs.input.checked=true;
	      this.$emit('input', value )
	    },
	    isChecked(){
	     	return this.value==this.$refs.input.value;
	    },
	    checkedThis(){
	    	this.$refs.input.checked=true;
	    	this.$emit('input', this.val )
	    }
	   
	}
}



