<template>
	<yd-layout class="bg">
		<yd-navbar slot="navbar" title="用户列表">
			<span @click="historyList" slot="left">
				<yd-navbar-back-icon></yd-navbar-back-icon>
			</span>
			<span slot="left" @touchstart.prevent="cancel"><i class="icon-close"></i></span>
			<span slot="right" v-if="selectAmount > 0">已选{{selectAmount}}人</span>
		</yd-navbar>
		<div class="searchUser">
			<div>
				<i class="btnSearch yd-icon-sousuo"></i>
				<input 
					type="text"
					v-model.lazy="searchName" 
					@change="searchByName"
					placeholder="搜索人员" 
				/>
			</div>
		</div>
		<yd-pullrefresh :on-infinite="loadList" ref="pullrefreshList_">
			<yd-list theme="4" v-if="list && list.length>0">
				<rx-list-item v-for="(item,index) in list" :key="index">
					<div slot="title" v-if="item.userId" class="rowBox">
						<span v-if="item.photo" class="userImag">
							<span :class="[single?'imgBoxRadio':'imgBoxCheck']">
								<img slot="img" class="img" :src="getFilePath(item.photo)" />
							</span>
							<span class="username">{{item.fullname}}</span>
						</span>
						<span v-else class="userImag">
							<span :class="[single?'imgBoxRadio':'imgBoxCheck']">
								<img slot="img" class="img" src="../../../static/img/hy_default.jpg" />
							</span>
							<span class="username">{{item.fullname}}</span>
						</span>
						<rx-radio 
							:val="item.userId" 
							v-model="selectUserId" 
							@click.native="handleClick(item)"
							name="chkUser" 
							v-if="single"
						>
						</rx-radio>
						<rx-check :val="item.userId" v-model="selectUserId" v-else></rx-check>
					</div>
					<div slot="title" v-else-if="item.dimId" @click="handleClick(item)">
						<span>{{item.name}}</span>
						<i class="yd-icon-right"></i>
					</div>
					<div slot="title" v-else-if="item.groupId" @click="handleClick(item,'index1111')">
						<span>{{item.name}}</span>
						<i class="yd-icon-right" v-if="item.childs > -1"></i>
					</div>
				</rx-list-item>
			</yd-list>
			<yd-list theme="4" v-else>
				<rx-list-item>
					<span slot="title" class="list-title">还没有任何数据</span>
				</rx-list-item>
			</yd-list>
		</yd-pullrefresh>
		<div 
			slot="tabbar"  
			:class="{'bright': selectAmount > 0}"
			class="bottomBtn"
		>
			<input 
				type="button"  
				@click="ok"
				value="完成"
			/>
		</div>
	</yd-layout>
</template>

<script>
	import Vue from 'vue'
	import listItem from '@/components/common/ListItem';
	import radio from '@/components/common/rxRadio';
	import checkbox from '@/components/common/rxCheckbox';
	import {loadRefreshList} from '@/assets/app.js';
	import router from '@/router/index';
	import {RxUtil} from "@/assets/util.js";
	
	Vue.component(listItem.name, listItem);
	Vue.component(radio.name, radio);
	Vue.component(checkbox.name, checkbox);
	
	export default {
		name : "rx-sysuser",
		data(){
		    return {
		      	list:[],
				page:0,
				total:0,
				searchName:"",
				merage:true,
				historyParams:[],
				
				selectUserId:"",
				selectedUser:[]
		    }
		},
		computed:{
			showBar:function(){
				return this.$store.state.pageModule.keyboard;
			}
		},
		props:{
		  	single:{
		  		type: Boolean, 
		  		default: true
		  	}
		},
		created() {
  			this.searchName="";
	  		this.historyParams=[];
		},
		computed: {
	  		classBtn: function () {
	  			var rtn={
	      			single: this.single,
	      			multity: !this.single
	    		};
	    		return rtn;
	  		},
	  		selectAmount: function(){
		    	if(!this.selectUserId) return 0;
		    	return this.selectUserId.split(",").length;
		    }
		},
		methods:{
		    loadList(){
		    	var url=_baseUrl +"/mobile/oa/getDimen.do";
				var params="pageIndex="+this.page+"&pageSize=10";
				this.loadLevel(params,url);
	
		    },
		    search(){
		    	this.page=0;
		    	this.merage=false;
		    	this.loadList();
		    },
		    cancel(){
		    	this.$emit('cancel');
		    },
		    //设置选中的帐号。
		    setValue(val){
		    	this.selectUserId=val;	    	
		    },
		    handleClick(item,index){
		    	console.log(index)
		    	console.log(item)
		    	if(item.dimId){
		    		var url=_baseUrl +"/mobile/oa/getGroup.do";
		    		var params={
		    			dimId:item.dimId
		    		};
					this.loadLevel(params,url);
		    	} else if(item.groupId&&item.childs>0){
		    		var url=_baseUrl +"/mobile/oa/getGroup.do";
	 				var params={
		    			groupId:item.groupId
		    		};
					this.loadLevel(params,url);
		    	} else if(item.groupId&&(item.childs==0||item.childs==null)){
		    		var url=_baseUrl +"/mobile/oa/getUsers.do";
	 				var params={
		    			groupId:item.groupId
		    		};
					this.loadLevel(params,url);
		    	}
		    	//this.$emit('ok',this.selectedUser);
		    	//console.log(1111111)
		    },
		    loadLevel(params,url){
				this.$ajax({
					url: url,
					method: 'POST',
					params:params
				}).then(response => {
					this.list = response.data.rowList;
					var flag=false;
					for(var i=0;i<this.historyParams.length;i++){
						var arr=this.historyParams[i];
						if(arr.params==params&&arr.url==url){
							flag=true;
						}
					}
					if(!flag){//不包含
						var obj={};
						obj.params=params;
						obj.url=url;
						this.historyParams.push(obj);
					}
				}).catch(error => {
					console.log(error);
				});
		    },
		    historyList(){
		    	if(this.historyParams.length>=2){
		    		var obj=this.historyParams[this.historyParams.length-2];
		    		this.loadLevel(obj.params,obj.url);
		    		this.historyParams.splice(this.historyParams.length-1,1);
		    	} else if (this.historyParams.length>=1) {
		    		this.cancel();
		    	}
		    },
		    searchByName(){
		    	if(this.searchName){
		    		var url=_baseUrl +"/mobile/oa/getUsersSearch.do";
					var params="";
					if(this.searchName) {
						params="&Q_fullName_S_LK=" + this.searchName;
					}
					this.$ajax.post(url, params).then(response => {
						this.list=response.data.rowList;
					})
					.catch(function(error) {
					});
		    	}
		    },
			getFilePath(fileId) {
				return _baseUrl + "/sys/core/file/previewFile.do?fileId=" + fileId;
			},
		    ok(){
		    	this.$emit('ok',this.selectedUser);
		    	this.$store.commit('srtFromUser', this.selectedUser);
		    	console.log(this.selectedUser)
		    }
	    
		},
		watch: {
	    	selectUserId: function (val, oldVal) {
	    		if(!val){
	    			this.selectedUser=[];
	    			return;
	    		}
	    		//处理增加的用户，意思是当前选择的用户不在选择的列表中。
	    		var aryVal=this.selectUserId.split(",");
	    		var selectMap=RxUtil.covertList2Map("userId", this.selectedUser);
	    		var allMap=RxUtil.covertList2Map("userId", this.list);
	    		//当前用户不在选中的用户中，添加
	    		for(var i=0;i<aryVal.length;i++){
	    			var tmp=aryVal[i];
	    			if(!selectMap[tmp]){
	    				this.selectedUser.push(allMap[tmp]);
	    			}
	    		}
	    		//删除已选
	    		for(var i=this.selectedUser.length-1;i>=0;i--){
	    			var o=this.selectedUser[i];
	    			var id=o["userId"];
	    			var isIn=RxUtil.isInAry(aryVal,id);
	    			if(!isIn){
	    				this.selectedUser.splice(i,1);
	    			}
	    		}
	    		
	    	}
    	}
	}
</script>

<style scoped>
	.bg{
		background: #f5f5f5;
	}
	
	.searchUser{
		padding: .2rem;
	}
	
	.searchUser>div{
		background: #fff;
		border-radius: 4rem;
	}
	
	.searchUser>div::after{
		content: '';
		display: block;
		clear: both;
	}
	
	.searchUser>div i{
		float: left;
		padding: .12rem;
	}
	
	.searchUser>div i:before{
		font-size: .4rem;
		color: #666;
	}
	
	.searchUser>div>input{
		float: left;
		width: calc(100% - .84rem);
		line-height: .68rem;
		font-size: .3rem;
		color: #666;
	}
	
	.single{
		text-align: center;
		margin-top: 2px;
		padding:0px 0px 2px 0px;
	}
	
	.multity{
		text-align: right;
		margin-top: 2px;
		padding:0px 0px 2px 0px;
		margin-right: 10px;
	}
	
	.div-selected{
		text-align: left;
		width: 2rem;
	}
	
	.div-selected i{
		font-weight: bold;
		color: red;
	}
	
	.rowBox{
	    display: flex;
	    align-items: center;
	}
	
	.rowBox>span{
		flex-grow: 1;
	}
	
	.rowBox>label{
		padding: 0;
	}
	
	.rowBox>>>.yd-checkbox-icon {
		border-radius: .08rem !important;
	}
	
	.yd-popup-center .imgBoxCheck {
		border-radius:.12rem;
		overflow:hidden;
	}
	
	.userImag{
	    display: flex;
	    align-items: center;
	}
	
	.imgBoxRadio{
	    width: .8rem;
	    height: .8rem;
	    display: block;
	    border-radius: .12rem;
	    overflow: hidden;
	}
	
	.imgBoxCheck{
	    width: .8rem;
	    height: .8rem;
	    display: block;
	    overflow: hidden;
	}
	
	.img{
	    width: 100%;
	    height: auto;
	}
	
	.username{
    	margin-left: .12rem;
	}
	
	.yd-scrollview .yd-list-theme4 .yd-list-item{
		padding: .2rem;
	}
	
	.yd-list-item{
		width: 100%;
	}

	.yd-list-theme4 >>> .yd-list-mes .yd-list-title{
		width: 100%;
	}
	
	.yd-list-theme4 >>> .yd-list-title > div{
		display: flex;
		justify-content:space-between;
	}
	
	.yd-icon-right{
		font-size: 0.4rem;
	}
	
	.frmContainer >>> .yd-navbar-item>a{
		min-width: 10%;
	}
	
</style>



