<template>
	<yd-layout >
    	<yd-navbar slot="navbar" title="审批任务">
      		<a href="javascript:;" @click="goMyToDo" slot="left">
        	<yd-navbar-back-icon></yd-navbar-back-icon>
      	</a>
    </yd-navbar>
    
    <ul class="task-nodes">
		<li class="node" v-for="(node,index) in destNodeUserList">
			<dl  v-if="node.taskNodeUser">
				<dd>
					<ol>
						<li class="nodeId">
							<rx-radio name="nodeId" :val="node.nodeId" v-model="destNodeId" v-if="allowSelectPath" >{{node.nodeText}}</rx-radio>
							<h1 v-if="!allowSelectPath">{{node.nodeText}}</h1>
						</li>
						<li class="userId">
							<rx-boxs v-model="node.taskNodeUser.userJsons"  
							:allowSelect="allowSelectUser" 
							containstyle="width:2.2rem;" textField="text" 
							@select="selectUserDialog" valField="id" v-if="!isReachEndEvent"></rx-boxs>
						</li>
					</ol>
				</dd>
			</dl>
			<dl v-else>
					<dd>
						<rx-radio name="nodeId" :val="node.nodeId" v-model="destNodeId" v-if="allowSelectPath">{{node.nodeText}}</rx-radio>
						<h1 v-if="!allowSelectPath">{{node.nodeText}}</h1>
					</dd>
					<dd class="node-s">
						<div  v-for = "(item,key) in node.fllowNodeUserMap" >
							<ol>
								<li>
									<h1>{{item.nodeText}}</h1>
								</li>
								<li>
									<rx-boxs v-model="item.userJsons"  
										:allowSelect="allowSelectUser"  
										containstyle="width:2.2rem;" textField="text" 
										@select="selectUserDialog" valField="id" v-if="!isReachEndEvent"></rx-boxs>
								</li>
							</ol>
						</div>
					</dd>
			</dl>
		</li>
	</ul>
	
	<ul class="task-nodes">
		<li class="node" >
			<dl >
				<dd>
					<ol>
						<li class="nodeId">
							<rx-radio val="AGREE"  v-model="jumpType" name="jumpType"  >通过</rx-radio>
					        <rx-radio val="BACK"  v-model="jumpType" name="jumpType"  > 驳回</rx-radio>
					        <rx-radio val="BACK_TO_STARTOR"  v-model="jumpType" name="jumpType"  >驳回发起人 </rx-radio>
    					</li>
    				</ol>
    			</dd>
    		</dl>
    	</li>
   </ul>
     
	<yd-cell-group>
      	
        <yd-cell-item  type="label">
          <select slot="right" @change="changeOpinion">
            <option value="">选择</option>
            <option value="同意">同意</option>
            <option value="不同意">不同意</option>
          </select>
        </yd-cell-item>
        <yd-cell-item>
          <yd-textarea slot="right" placeholder="请输入意见" maxlength="200" :value="opinion"></yd-textarea>
        </yd-cell-item>
        <yd-cell-item type="label">
          <rx-attachment  :readonly="false" slot="right" v-model="files"></rx-attachment>
        </yd-cell-item>
        
     
    </yd-cell-group>
    
  
    
    <yd-tabbar slot="tabbar">
		<tabbar-item title="确定" name="approve" type="a" v-on:handclick="handApprove()" icon="yd-icon-approve"
                   :active="true"></tabbar-item>
        <tabbar-item title="取消" name="cancel" type="a" v-on:handclick="cancel()" icon="yd-icon-cancel" ></tabbar-item>
    </yd-tabbar>
    
    <yd-popup v-model="showUserDialog" position="center" width="100%">
       <rx-sysuser :single="false" ref="sysUser" @ok="selectUser" @cancel="showUserDialog=false;"></rx-sysuser>
    </yd-popup>

  
  </yd-layout>
</template>

<script>
	
import Vue from 'vue'

import sysuser from '@/components/common/SysUser';
import rxboxs from '@/components/common/rx-boxs';
import RxAttachment from '@/components/common/form/RxAttachment';

import router from '@/router/index';
import {RxUtil} from "@/assets/util.js";
import { convertBoJson } from '@/assets/app.js';


Vue.component(sysuser.name, sysuser);
Vue.component(rxboxs.name, rxboxs);
Vue.component(RxAttachment.name, RxAttachment);

var actions={'AGREE':{name:'通过',opinion:'同意'},'BACK':{name:'驳回',opinion:'驳回'},'BACK_TO_STARTOR':{name:'驳回',opinion:'驳回'}};

export default{
    name : "rx-approve",
    data(){
      return {
	    	bpmTask :{},
	    	destNodeUserList:[],
	    	isReachEndEvent:false,
	    	taskConfig:{},
	    	
	    	showUserDialog:false,
	    	opinion:"同意",
	    	//目标节点
	        destNodeId:"",
	        showUser:false,
	        //选中的的rx-boxs实例，用于传递数据。
	        nodeUserVm:{},
	        files:"[]",
	        jumpType:"AGREE"
	       
    	}
    },
    mounted() {
	   var data= this.$store.state.bpmModule.taskData;
	   this.bpmTask=data.bpmTask;
	   this.destNodeUserList=data.destNodeUserList;
	   this.isReachEndEvent=data.isReachEndEvent;
	   this.taskConfig=data.taskConfig;
	},
	computed:{
		allowSelectPath:function(){
			var tmp=this.taskConfig.allowPathSelect;
			if(tmp=="true" && this.destNodeUserList.length>1){
				return true;
			}
			return false;	
		},
		allowSelectUser:function(){
			var tmp=this.taskConfig.allowNextExecutor;
			return tmp=="true"?true:false;
		}
	},
	methods: {
      changeOpinion(e){
        var v=e.srcElement.value;
        this.opinion=v;
      },
      
      /**
       * 打开用户对话框
       */
      selectUserDialog(vm){
      	this.showUserDialog=true;
      	this.$refs.sysUser.search();
      	// 设置选择用户。
      	this.$refs.sysUser.setValue("");
      	//设置选中的rx-box 控件，用于传递数据。
      	this.nodeUserVm=vm;
      },
      /**
       * 点击用户对话框选择按钮，处理用户对话框返回数据。
       */
      selectUser(data){
      	var list=[];
      	for(var i=0;i<data.length;i++){
      		var obj=data[i];
      		var o={};
      		o.id=obj.userId;
      		o.text=obj.fullname;
      		list.push(o);
      	}
      	var json=JSON.stringify(list);
      	
      	this.nodeUserVm.setValue(json);
      	this.showUserDialog=false;
      },
      handApprove() {
      	console.info(this.jumpType);
      	this.approve(this.jumpType);
      },
      //[{id:"",text:""},{id:"",text:""}]
      getUserIds(userJsons){
      	var jsonAry=eval(userJsons);
      	var idAry=[];
      	if(!jsonAry || jsonAry.length==0) return "";
      	for(var i=0;i<jsonAry.length;i++){
      		var o=jsonAry[i];
      		idAry.push(o.id);
      	}
      	return idAry.join(",");
      },
      /**
       * 获取选择的用户。
       */
      getUserJson(nodeList,destNodeId){
      	var nodeUsers=[];
	    for(var i=0;i<nodeList.length;i++){
	    	var node=nodeList[i];
	    	var nodeUser=node.taskNodeUser;
	    	if(nodeUser){
	    		var userJsons=nodeUser.userJsons;
	    		var userIds=this.getUserIds(userJsons);
	    		if(!userIds) continue;
	    		var tmpUser={nodeId:nodeUser.nodeId,userIds:userIds};
	    		if(destNodeId){
	    			if(destNodeId==nodeUser.nodeId){
	    				nodeUsers.push(tmpUser);
	    			}
	    		}
	    		else{
	    			nodeUsers.push(tmpUser);
	    		}
	    	}
	    	else{
	    		for(var nodeId in node.fllowNodeUserMap){
	    			var user=node.fllowNodeUserMap[nodeId];
	    			var userJsons=user.userJsons;
	    			var userIds=this.getUserIds(userJsons);
	    			if(!userIds) continue;
	    			var tmpUser={nodeId:user.nodeId,userIds:userIds};
		    		if(destNodeId){
		    			if(destNodeId==node.nodeId){
		    				nodeUsers.push(tmpUser);
		    			}
		    		}
		    		else{
		    			nodeUsers.push(tmpUser);
		    		}
	    		}
	    	}
	    }
	    return JSON.stringify(nodeUsers);
      },
      /**
       * 构建cmd数据。
       */
      getProcessCmd(action){
      	var task=this.bpmTask;
      	var approveCmd={};
	    var obj=actions[action];
	    approveCmd.taskId=task.id;
	    approveCmd.jumpType=action;
	    approveCmd.opinion=this.opinion;
	    approveCmd.isValid=true;
	    approveCmd.message="success";
	    
	    var data= this.$store.state.bpmModule.taskData;
	    
	    var jsonData=convertBoJson(data.formModels);
	    
	    
	    approveCmd.jsonData=JSON.stringify(jsonData);
	    approveCmd.opFiles=this.files;
	    
	    var nodeList=this.destNodeUserList;
	    if(nodeList.length>1 && !this.destNodeId){
	    	approveCmd.isValid=false;
	    	approveCmd.message="没有选择目标节点";
	    	return approveCmd;
	    }
	    //[{nodeId:"",userIds:""},{nodeId:"",userIds:""}]
	    if(this.destNodeId){
	    	approveCmd.destNodeId=this.destNodeId;
	    }
	    
	    var destNodeUsers= this.getUserJson(nodeList,this.destNodeId);
	    approveCmd.destNodeUsers=destNodeUsers;
	  
	    return approveCmd;
      },
      cancel(){
      	router.go(-1);
      },
      approve(action){
	      var approveCmd=this.getProcessCmd(action);
	      //获取数据失败。
	      if(!approveCmd.isValid){
	      	this.errorToast(approveCmd.message)
	      	return;
	      }
	      delete approveCmd.isValid;
	      delete approveCmd.message;
	      
	      
	      var url=_baseUrl +"/bpm/core/bpmTask/doNext.do";
	      var params=RxUtil.serialJsonToForm(approveCmd);
	
	      this.$ajax.post(url,params).then(response =>{
	        this.$dialog.loading.close();
	        var data=response.data;
	        if(data.success){
	        	this.successToast("操作成功！",500,'myToDo');
	        }
	        else{
	        	this.errorToast("操作失败！")
	        }
	      })
	      
      },
      goMyToDo(){
      	router.push({name:"myToDo"});
      }
	}
}	


</script>

<style scoped>
	.yd-flexbox-item-center{
		text-align: center;
		margin-top: 2px;
		padding:0px 5px 0px 5px;
	}
	
	.yd-radio-text{
	    overflow: hidden;
	    text-overflow: ellipsis;
	    white-space: nowrap;
	    width: 2.5rem;
	   
	    display: inline-block;
	}
	
	
	.btn-select{
    	display: table-cell;height: 44px;vertical-align: middle;
  	}

	.cell-left{
	    width: 4rem;
	    height: 1.55rem;
	    overflow-x: auto;
	}
	
	
</style>


